
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;


}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  font-size:1.6rem;

}
h1{
  font-size: 6rem;

}

.app{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgb(0, 0,0,0.4);
  color: #fff;
  
}

.app:before{
  content: '';
  background: url('./assets/sunset.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}


.container{
  max-width: 700px;
  height: 700px;
  margin:auto ;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.app.top{
  width: 100%;
  margin: 1rem auto;
  }

  .app .discreiption{
    position:relative;
    right:-90%;
    transform-origin: 0 0;
    transform: rotate(269deg);
  }
  .app .bottom{
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    width: 100%;
    margin: 1rem;
    padding: 1rem;
    border-radius: 12px;
    background-color: rgba(225, 225, 225, 0.2);
  }

  .bold{
    font-weight: 700;
  }

.app .search{
  text-align:center;
  padding: 1rem;
}

.app input {
  padding: .7rem 1.5rem;
  font-size :1.2rem;
  border-radius: 25px;
  border: 1px solid;
  background: rgba(225, 225, 225, 0.1);
  color: #fff;
}

::placeholder{
  color: #fff;
}
   